@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.2.0/css/font-awesome.min.css);

body {
  margin: 0;
  overflow-y: hidden;
  overflow-x: hidden;
  background: rgb(122,134,147);
  background: linear-gradient(15deg, rgba(122,134,147,1) 49%, rgba(4,2,0,1) 100%); 
}
.header {
  display: grid;
  margin: 0 auto;
  grid-template-columns: 10% 70% 20%;
  width: 100%;
  height: 100px;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  justify-items:center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  border-bottom: 1px solid #ff6e51;
  grid-template-areas:
  "logo empty reg-sign"
}

.empty{
  grid-area: empty;
  width: 100%;
}

.logo {
  color: #ff6e51;
  width: 100%;
  font-size: 32px;
  text-align: center;
  grid-area: logo;
}

.reg-sign{
  display: grid;
  grid-template-columns: 50% 50%;
  width: 100%;
  grid-area: reg-sign;
}

.registration{
  border-right: 2px solid #ed5739;
  color: #ed5739;
  font-size: 30px;
  padding-right: 20px;
  width: 100%;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
}

.sign-in {
  color: #ff6e51;
  font-size: 30px;
  width: 100%;
  text-align: center;
}

@media (max-width: 1500px) {
  .header {
    grid-template-columns: 15% 40% 25%;
  }
}

@media (max-width: 1220px) {
  .header {
    grid-template-columns: 25% 40% 35%;
  }
}

@media (max-width: 878px) {
  .header {
    grid-template-columns: 35% 20% 45%;
  }
}

@media (min-width:500px) and (max-width: 678px) {
  .header {
    grid-template-columns: 45% 0% 50%;
  }

  .logo {
    font-size: 30px;
  }

  .registration {
    font-size: 24px;
  }
  
  .sign-in {
    font-size: 24px;
  }
}
.content-wrap{
  width: 100vw;
  height: 100vw;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
*{
	font-family: 'Open Sans', 'sans-serif', 'FontAwesome';
}
body{
    background: rgb(52, 56, 61);
}

.hidden{
  display: none;
}

h1{
	color: rgb(255, 255, 255);
	margin: 20px auto 0;
	width: 200px;
	text-align: center;
}
#wrapper-register{
	position: absolute;
	width: 320px;
	left: 50%;
	margin-left: -160px;
	top: 50%;
	margin-top: -75px;
}

#wrapper-register a {
  text-decoration: none;
  cursor: pointer;
  font-size: 24px;
  color: #ed5739;
}

/* === Sign in Form === */
#register {
	height: 90px;
	width: 300px;
	border-radius: 8px;
	position: relative;
}
#register::before {
	display: block;
	position: relative;
	height: 2px;
	background: rgb(52, 56, 61);
	content: '';
	top: 44px;
	margin-left: 20px;
	z-index: 1;
}
#register input:first-of-type{
	border-top-right-radius: 8px;
	border-top-left-radius: 8px;
}
#register input:last-of-type{
	border-bottom-right-radius: 8px;
	border-bottom-left-radius: 8px;
}
#register  input[type="text"], #register  input[type="password"], #register .submit{
	background: rgb(28, 30, 33);
	box-shadow: inset -100px -100px 0 rgb(28, 30, 33); /*Prevent yellow autofill color*/
	color: rgb(52, 56, 61);
}
#register  input[type="text"], #register  input[type="password"]{
	position: relative;
	display: block;
	width: 280px;
	height: 45px;
	border: 0;
	outline: 0;
	top: -2px;
	padding: 0 0 0 20px;
	font-weight: 700;
}
#register  input[type="text"]:focus, #register  input[type="password"]:focus{
	color: rgb(255, 255, 255);
}
#register .submit{
  display: block;
  position: absolute;
  width: 30px;
  height: 35px;
  color: rgb(52, 56, 61);
  border-radius: 100%;
  outline: 0;
  z-index: 2;
  top: 19px;
  right: -24px;
  border: 6px solid rgb(52, 56, 61);
  font-size: 25px;
  text-indent: 0px;
  padding-left: 6px;
  padding-bottom: 1px;
  text-align: center;
}
#register .submit:hover{
	color: #ed5739;
	text-shadow: 0 0 10px #ed5739;
	cursor: pointer;
}
#register p {
	color: rgb(79, 85, 97);
	padding: 0 20px;
	font-weight: 700;
	font-size: 12px;
	margin: 5px 0 0 0;
}
#register p > a{
	color: rgb(111, 119, 135);
	text-decoration: none;
}
#register p > a:hover{
	border-bottom: 1px solid;
}

#register-succesful{
	position: absolute;
	left: 50%;
	margin-left: -200px;
	top: 50%;
	margin-top: -80px;
	width: 370px;
	height: 160px;
	border: 1px solid #ed5739;
	border-radius: 20px;
}

#register-succesful h1{
	position: relative;
	top: 35px;
	color: #ed5739;
	width: 100%;
}
